import React, { useState, useEffect } from 'react';
import MatchGoalsPresentational from './Presentational';
import useFetchApi from '../../hooks/useFetchApi';
import Spinner from '../../components/Elements/Spinner';

const MatchGoalsContainer = () => {
  const [loading, setLoading] = useState<boolean>(true);
  const { data, error, isLoading } = useFetchApi(
    'stats.php?teams&type=over&limit=20&search=2.5'
  );

  useEffect(() => {
    if (!data || isLoading) {
      setLoading(true);
    } else {
      setLoading(false);
    }
  }, [data, isLoading]);

  if (error) return <p>error</p>;
  if (loading) return <Spinner />;
  return <MatchGoalsPresentational data={data} isLoading={isLoading} />;
};

export default MatchGoalsContainer;
