import React from 'react';
import ReactTable from 'react-table';
import './react-table.css';
import RightArrow from '../../components/icons/RightArrow';
import LeftArrow from '../../components/icons/LeftArrow';
import CustomPagination from './CustomPagination';
import { IconButton } from '@material-ui/core';
import { ArrowBackRounded, ArrowForwardRounded } from '@material-ui/icons';
import { withStyles } from '@material-ui/styles';
import styled from 'styled-components';

const WhiteIconBut = withStyles({
  root: {
    color: '#fff'
  }
})(IconButton);
const NextButton = props => (
  <WhiteIconBut {...props}>
    <ArrowForwardRounded />
  </WhiteIconBut>
);
const PrevButton = props => (
  <WhiteIconBut {...props}>
    <ArrowBackRounded />
  </WhiteIconBut>
);
const defaultButton = (
  props: JSX.IntrinsicAttributes &
    React.ClassAttributes<HTMLButtonElement> &
    React.ButtonHTMLAttributes<HTMLButtonElement>
) => <button {...props}>{props.children}</button>;

type paginationProps = {
  // Computed
  pages;
  // Props
  page;
  previousText;
  nextText;
  onPageChange;
  pageComponent;
  showPageSizeOptions;
  pageSizeOptions;
  pageSize;
  data;
  showPageJump;
  canPrevious;
  canNextFromData;
  onPageSizeChange;
  className;
  PreviousComponent;
  NextComponent;
  showTotalPages;
};
const PaginIconButton = withStyles({
  root: {
    color: '#fff',
    padding: 8,
    '&:disabled': {
      '& *': {
        color: '#aaa'
      },
      cursor: 'not-allowed',
      pointerEvents: 'unset'
    }
  }
})(IconButton);

const PaginationWrapper = styled.div`
  display: flex;
  width: 100%;
  margin-top: 10px;
`;
export class Pagination extends React.Component<
  paginationProps,
  { visiblePages: any }
> {
  constructor(props: paginationProps) {
    super(props);

    this.changePage = this.changePage.bind(this);

    this.state = {
      visiblePages: this.getVisiblePages(null, props.pages)
    };
  }

  componentWillReceiveProps(nextProps: { pages: any; page: number }) {
    if (this.props.pages !== nextProps.pages) {
      this.setState({
        visiblePages: this.getVisiblePages(null, nextProps.pages)
      });
    }

    this.changePage(nextProps.page + 1);
  }

  filterPages = (
    visiblePages: { filter: (arg0: (page: any) => boolean) => void },
    totalPages: number
  ) => {
    return visiblePages.filter(page => page <= totalPages);
  };

  getVisiblePages = (page: number, total: number) => {
    if (total < 7) {
      return this.filterPages([1, 2, 3, 4, 5, 6], total);
    } else {
      if (page % 5 >= 0 && page > 4 && page + 2 < total) {
        return [1, page - 1, page, page + 1, total];
      } else if (page % 5 >= 0 && page > 4 && page + 2 >= total) {
        return [1, total - 3, total - 2, total - 1, total];
      } else {
        return [1, 2, 3, 4, 5, total];
      }
    }
  };

  changePage(page: number) {
    const activePage = this.props.page + 1;

    if (page === activePage) {
      return;
    }

    const visiblePages = this.getVisiblePages(page, this.props.pages);

    this.setState({
      visiblePages: this.filterPages(visiblePages || [], this.props.pages)
    });

    this.props.onPageChange(page - 1);
  }

  render() {
    // const { PageButtonComponent = defaultButton } = this.props;
    const { visiblePages } = this.state;
    const activePage = this.props.page + 1;

    return (
      <PaginationWrapper>
        <div className='Table__prevPageWrapper'>
          <PaginIconButton
            onClick={() => {
              if (activePage === 1) return;
              this.changePage(activePage - 1);
            }}
            disabled={activePage === 1}
            aria-label={this.props.previousText}
          >
            <ArrowBackRounded />
          </PaginIconButton>
        </div>
        <div className='Table__nextPageWrapper'>
          <PaginIconButton
            onClick={() => {
              if (activePage === this.props.pages) return;
              this.changePage(activePage + 1);
            }}
            disabled={activePage === this.props.pages}
            aria-label={this.props.nextText}
          >
            <ArrowForwardRounded />
          </PaginIconButton>
        </div>
      </PaginationWrapper>
    );
  }
}

const DataTable = ({ data = [], columns = [] }) => {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  // useEffect(() => {

  //   return () => {
  //     console.log('unount')
  //   }
  // }, [page, rowsPerPage])
  const getPages = (rpp?) => {
    return Math.ceil(data.length / (rpp || rowsPerPage));
  };

  function handleChangePage(pageIndex) {
    setPage(pageIndex);
  }

  function handleChangeRowsPerPage(pageSize, pageIndex) {
    const newRows = parseInt(pageSize, 10);
    setRowsPerPage(newRows);
    const pages = getPages(newRows);
    // console.log(pages, page)
    if (pages <= page) {
      setPage(pageIndex);
    }
  }
  // console.log(data)
  return (
    <ReactTable
      data={data}
      resolveData={data => {
        return data.map(item => {
          item.team.last6 = item.team.last6.map(x => ({
            value: x,
            tooltip: x ? 'Won' : 'Lost'
          }));
          return item;
        });
      }}
      pageSizeOptions={[5, 10]}
      // manual
      resizable={false}
      columns={columns.length > 0 ? columns : undefined}
      defaultPageSize={5}
      pages={getPages()}
      sortable={false}
      page={page} // the index of the page you wish to display
      pageSize={rowsPerPage} // the number of rows per page to be displayed
      // showPageSizeOptions={false}
      // Callbacks
      onPageChange={handleChangePage}
      onPageSizeChange={handleChangeRowsPerPage}
      // Overriden Components
      PaginationComponent={CustomPagination}
      // NextComponent={NextButton}
      // PreviousComponent={PrevButton}
      // onSortedChange={(newSorted, column, shiftKey) => {... }} // Called when a sortable column header is clicked with the column itself and if the shiftkey was held. If the column is a pivoted column, `column` will be an array of columns
      // onExpandedChange={(newExpanded, index, event) => {... }} // Called when an expander is clicked. Use this to manage `expanded`
      // onFilteredChange={(filtered, column) => {... }} // Called when a user enters a value into a filter input field or the value passed to the onFiltersChange handler by the Filter option.
      // onResizedChange={(newResized, event) => {... }}
    />
  );
};

export default DataTable;
