import React from 'react';
import styled from 'styled-components';
import HexagonBadge from '../../components/Elements/HexagonBadge';
import theme from '../../styles/theme';
import TeamCrest from '../../components/Elements/TeamCrest';
import Tooltip from '../../components/Elements/Tooltip';

const BadgeContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  .BagdeWrapper {
    margin-right: 6px;
  }
`;

const ValueBadgeContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

interface IParagraphProps {
  children: any | any[];
  align?: string;
  marginRight?: number;
  wrap?: boolean | undefined;
  top?: number | undefined;
  title?: string | undefined;
}

const Paragraph = ({
  children,
  wrap,
  ...passthroughProps
}: {
  children: any | any[];
  wrap?: boolean | undefined;
  top?: number | undefined;
  align?: string | undefined;
  marginRight?: number | undefined;
  title?: string | undefined;
  className?: string | undefined;
}) => {
  return (
    <StyledParagraph
      className={'Paragraph' + ' ' + wrap ? 'wrap' : ''}
      {...passthroughProps}
    >
      {children}
    </StyledParagraph>
  );
};

const StyledParagraph = styled.p<IParagraphProps>`
  &.wrap {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  position: relative;
  color: #fff;
  font-family: 'Gothic A1', sans-serif;
  font-size: 14.4px;
  text-align: ${({ align }) => (align ? align : 'center')};
  margin-right: ${({ marginRight }) =>
    marginRight ? marginRight.toString() + 'px' : '0'};
  top: ${({ top }) => (top ? top.toString() + 'px' : 'inherit')};
`;

const ParagraphGreen = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: #0dfa89;
  font-family: 'Open Sans', sans-serif;
  text-align: ${({ align }: { align?: string | undefined }) =>
    align ? align : 'center'};
`;

interface ITableHeaderProps {
  paddingLeft?: number | undefined;
  paddingRight?: number | undefined;
  paddingTop?: number | undefined;
  paddingBottom?: number | undefined;
  align?: string | undefined;
  color?: string | undefined;
}

const TableHeader = styled.div<ITableHeaderProps>`
  display: flex;
  font-family: 'Gothic A1', sans-serif;
  font-size: 14.4px;
  justify-content: ${({ align }) => {
    switch (align) {
      case 'left':
        return 'flex-start';
      case 'right':
        return 'flex-end';
      case 'center':
      default:
        return 'center';
    }
  }};
  align-items: center;
  color: ${({ color }) => (color ? color : '#FFF')};
  padding-left: ${({ paddingLeft }) =>
    paddingLeft ? paddingLeft.toString() + 'px' : '0'};
  padding-right: ${({ paddingRight }) =>
    paddingRight ? paddingRight.toString() + 'px' : '0'};
  padding-top: ${({ paddingTop }) =>
    paddingTop ? paddingTop.toString() + 'px' : '0'};
  padding-bottom: ${({ paddingBottom }) =>
    paddingBottom ? paddingBottom.toString() + 'px' : '0'};
  text-align: ${({ align }: { align?: string | undefined }) =>
    align ? align : 'center'};
  font-family: 'Open Sans', sans-serif;
  .tooltip {
    opacity: 1 !important;
  }
`;

const TeamName = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
`;

const columns = [
  // Team Column
  {
    Header: (
      <TableHeader align='left' paddingLeft={10}>
        # Team
      </TableHeader>
    ),
    width: 230,
    Cell: props => (
      <TeamName className='TeamName'>
        <Paragraph marginRight={10}>{props.index + 1} </Paragraph>
        <TeamCrest team={props.original.team} marginRight={10} size={25} />
        <Paragraph
          title={props.original.team.name}
          wrap={true}
          className='TeamParagraph'
          top={1}
        >
          {props.original.team.name}
        </Paragraph>
      </TeamName>
    )
  },
  // Percentage Column
  {
    Header: (
      <TableHeader className='TableHeader' align='center' paddingLeft={10}>
        <Tooltip content='Total % of games over'>%</Tooltip>
      </TableHeader>
    ),
    width: 70,
    Cell: props => (
      <ParagraphGreen>
        {Math.round(props.original.team.success_score * 10) / 10}%
      </ParagraphGreen>
    )
    // width: 80
  },
  // Number of Games column
  {
    Header: (
      <TableHeader className='TableHeader' paddingLeft={10}>
        <Tooltip content='Total # of games over.'>#</Tooltip>
      </TableHeader>
    ),
    width: 60,
    Cell: props => <Paragraph top={1}>{props.original.team.MP}</Paragraph>
    // width: 80
  },
  // ROI column
  {
    Header: (
      <TableHeader className='TableHeader' paddingLeft={20}>
        {/* <Tooltip
          content={
            <p>
              Return on investment from
              <br /> a £1 bet on this outcome in <br />
              each game within the "Last 6" range.
            </p>
          }
        >
          ROI £
        </Tooltip> */}
        ROI £
      </TableHeader>
    ),
    width: 60,
    Cell: props => (
      <ValueBadgeContainer>
        <HexagonBadge
          iconType='LOCK'
          size={25}
          // size={40}
          foregroundColor={theme.colors.darkGreen}
          backgroundColor='#fff68f'
          strokeColor='transparent'
        />
      </ValueBadgeContainer>
    )
  },
  {
    Header: (
      <TableHeader
        className='TableHeader'
        paddingLeft={5}
        color={theme.colors.lightGreen}
      >
        Last 6 Games ↓
      </TableHeader>
    ),
    width: 200,
    Cell: props => (
      <BadgeContainer>
        {props.original.team.last6.map(({ value, tooltip }, key) =>
          value ? (
            <HexagonBadge
              key={key}
              iconType='UP_ARROW'
              size={25}
              foregroundColor={theme.colors.darkGreen}
              backgroundColor='#FFF'
              strokeColor='#FFF'
              tooltipContent={tooltip}
              tooltipPosition='top'
            />
          ) : (
            <HexagonBadge
              key={key}
              iconType='DOWN_ARROW'
              size={25}
              foregroundColor='#FFF'
              backgroundColor='transparent'
              strokeColor='#FFF'
              tooltipContent={tooltip}
              tooltipPosition='bottom'
            />
          )
        )}
      </BadgeContainer>
    )
  },
  {
    Header: (
      <TableHeader align='right' paddingRight={0}>
        Next
      </TableHeader>
    ),
    width: 60,
    Cell: props =>
      props.original.team.next_match.name ? (
        <TeamCrest team={props.original.team} marginRight={10} />
      ) : (
        <img
          src='https://cdn.sportmonks.com/images//soccer/teams/13/13.png'
          width='25px'
          height='25px'
        />
      )
  }
];

export default columns;
