import React from 'react';
import Hexagon from 'react-hexagon';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: ${({ size }: { size: number }) =>
    size ? size.toString() + 'px' : '20px'};
  height: ${({ size }: { size: number }) =>
    size ? size.toString() + 'px' : '20px'};
  svg {
    width: ${({ size }: { size: number }) =>
      size ? size.toString() + 'px' : '20px'};
    height: ${({ size }: { size: number }) =>
      size ? size.toString() + 'px' : '20px'};
  }
`;

const HexagonSVG = ({
  size = 20,
  fill = '#FFF',
  stroke = 'transparent'
}: {
  size?: number;
  fill?: string;
  stroke?: string;
}) => {
  return (
    <Wrapper className='HexagonWrapper' size={size}>
      <Hexagon
        flatTop
        diagonal={size}
        style={{ stroke, fill, strokeWidth: 2 }}
      />
    </Wrapper>
  );
};

export default HexagonSVG;
