import React from 'react';
import styled from 'styled-components';
import { default as LockIcon } from '../icons/Lock';
import Hexagon from '../icons/Hexagon';
import UpArrow from '../icons/UpArrow';
import DownArrow from '../icons/DownArrow';
import Tooltip from './Tooltip';

const SVGWrapper = styled.div`
  position: relative;
  /* width: 50px; */
  /* height: 60px; */
  display: flex;
  justify-content: center;
  align-items: center;
`;

interface IIconStylerProps {
  size?: number;
}

const IconStyler = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  /* to position the icon centered in the docz's playground */
  display: inline-flex;
  transform: translate(-50%, -50%);
  width: ${({ size }: IIconStylerProps) =>
    size ? Math.round(size / 3).toString() + 'px' : '16px'};
  height: ${({ size }: IIconStylerProps) =>
    size ? Math.round(size / 3).toString() + 'px' : '16px'};
  svg {
    overflow: visible;
    width: ${({ size }: IIconStylerProps) =>
      size ? Math.round(size / 3).toString() + 'px' : '16px'};
    height: ${({ size }: IIconStylerProps) =>
      size ? Math.round(size / 3).toString() + 'px' : '16px'};
  }
`;

const renderIcon = (iconType: string, fill: string = 'red') => {
  switch (iconType.toUpperCase()) {
    case 'LOCK':
      return <LockIcon width={14} height={14} fill={fill} />;
    case 'UP_ARROW':
      return <UpArrow width={14} height={14} fill={fill} />;
    case 'DOWN_ARROW':
      return <DownArrow width={14} height={14} fill={fill} />;
    default:
      return <LockIcon width={14} height={14} fill={fill} />;
  }
};

const HexagonBadge = ({
  iconType = 'LOCK',
  foregroundColor = 'blue',
  backgroundColor = 'yellow',
  strokeColor = 'transparent',
  size = 45,
  tooltipContent = undefined,
  tooltipPosition = 'top'
}: {
  /**
   * Set this to change the badge icon type
   * @default LOCK
   */
  iconType?: 'LOCK' | 'UP_ARROW' | 'DOWN_ARROW';
  /**
   * Set this to change the badge foreground color
   * @default blue
   */
  foregroundColor?: string;
  /**
   * Set this to change the badge background color
   * @default yellow
   */
  backgroundColor?: string;
  /**
   * Set this to change the badge border/stroke color
   * @default transparent
   */
  strokeColor?: string;
  /**
   * Set this to change the badge size
   * @default 45
   */
  size?: number;
  /**
   * Set this to display a tooltip text on mouse hover over the badge
   * @default undefined
   */
  tooltipContent?: string;
  /**
   * Set this to change the display position of the tooltip text
   * @default top
   */
  tooltipPosition?: 'top' | 'bottom';
}) => {
  const renderSVG = () => (
    <SVGWrapper className='SVGWrapper BagdeWrapper'>
      <Hexagon size={size} fill={backgroundColor} stroke={strokeColor} />
      <IconStyler className='IconStyler' size={size}>
        {renderIcon(iconType, foregroundColor)}
      </IconStyler>
    </SVGWrapper>
  );

  if (tooltipContent) {
    return (
      // <Tooltip content={tooltipContent} position={tooltipPosition}>
      renderSVG()
      // {renderSVG()}
      // </Tooltip>
    );
  }

  return renderSVG();
};

export default HexagonBadge;
