import React from 'react';
import styled from 'styled-components';
import teams from '../../data/team.json';
import GreyCrest from '../icons/GreyCrest';

interface IImgProps {
  src: string;
  size?: number;
  marginRight?: number;
}

const Img = styled.img<IImgProps>`
  width: ${({ size }) => (size ? size.toString() + 'px' : '30px')};
  height: ${({ size }) => (size ? size.toString() + 'px' : '30px')};
  margin-right: ${({ marginRight }) =>
    marginRight ? marginRight.toString() + 'px' : ''};
`;

const getTeamCrestUrl = (acronym: string = ''): string => {
  const team: any = teams[acronym];
  if (team && team.crest) {
    return team.crest;
  }
  return '';
};

const TeamCrest = ({
  team,
  marginRight,
  size
}: {
  // TODO: create the Team Type or interface.
  team: any;
  marginRight?: number;
  size?: number;
}) => {
  // const crestUrl = getTeamCrestUrl(tea);
  if (!team.logo) return <GreyCrest size={size} marginRight={marginRight} />;
  return <Img marginRight={marginRight} size={size} src={team.logo} />;
};

export default TeamCrest;
