import React from 'react';
import greyCrest from '../../static/grey-crest.png';

interface IGreyCrestProps {
  size?: number | undefined;
  marginRight?: number | undefined;
}

const GreyCrest = ({ size = 30, marginRight = 0 }: IGreyCrestProps) => {
  return (
    <img width={size} height={size} style={{ marginRight }} src={greyCrest} />
  );
};

export default GreyCrest;
